* {
  margin: 0;
  padding: 0;
  font-family: nunito sans;
}

.dict {
  color: #ff2b6b;
  font-family: acme;
  letter-spacing: .12cm;
  font-weight: 800;
  font-size: 70px;
}

.chem {
  color: #dfc91f;
  font-family: acme;
  letter-spacing: .12cm;
  font-weight: 800;
  font-size: 70px;
}

.back {
  width: auto;
  max-width: 100vw;
  max-height: 90vh;


}



.App {

  min-height: 100vh;
  position: relative;

}

.Home {
  min-height: 100vh;
  position: relative;
  padding: 0vh 10vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home-box {
  // border: 1px solid #243257b7;
  width: 21cm;
  // padding: .0cm .0cm;
  padding: 0cm 0cm .25cm 0cm;
  border-radius: 5px;
  display: flex;
  backdrop-filter: blur(1px);
  flex-direction: column;

  align-items: center;
}

.name {
  color: white;
  font-family: acme;
  letter-spacing: .12cm;
  font-weight: 800;
  font-size: 50px;
}

.tagline {
  color: white;
  font-family: acme;
  letter-spacing: .12cm;
  font-weight: 800;
  font-size: 70px;
}

.desc {
  color: white;
  font-family: acme;
  font-size: 40px;
  text-align: center;
}

.Home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("back.jpg");
  background-repeat: no-repeat;
  background-size: auto 100vh;
  background-position: center center;
  filter: brightness(32%);
  background-color: #3a4c7d;
}

.start {
  background-color: transparent;
  border: 2.5px solid #00a0d9;
  background-color: #00a0d9;
  cursor: pointer;
  color: white;
  margin-top: 1cm;
  font-size: 20px;
  padding: .2cm .8cm;
  border-radius: 10px;
}

@media only screen and (max-width:900px) {
  .Home::before {
    background-size: 95vw auto;
  }
}